html{
  font-size: 100.5%;
  margin: auto;
  /* overflow: auto; */
  overflow: scroll;
  overflow-x: hidden;

  
}
/* html {
  overflow: scroll;
  overflow-x: hidden;
} */
::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

body {
  margin: 0;
  /* font-family: 300 14px/3 'Helvetica Neue', 'sans-serif'; */
  font-family: 'Times New Roman', Times, serif;
  color: white;
  background: #022c43;
  /* overflow: hidden; */
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

